import './WeOffer.scss';
import maps from './Maps';

function WeOffer() {
    return (
        <section id="WeOffer">
            <h1> </h1>
            <div className="container">
                <div className="card sushimaki">
                    <img src={maps['sushiMaki']} alt=""/>
                    <div className="text">
                        <p> </p>
                    </div>
                </div>
                <div className="card sushiset">
                    <img src={maps['sushiSet']} alt=""/>
                    <div className="text">
                        <p> </p>
                    </div>
                </div>
                <div className="card nigiri">
                    <img src={maps['nigiri']} alt=""/>
                    <div className="text">
                        <p></p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default WeOffer;